.root {
    padding: 135px 40px 40px;
    font-family: var(--font-lato);
    font-weight: 500;
    font-size: 12px;
    color: var(--c-white);
}

.rhombusRibbon {
    position: relative;
    overflow: hidden;
}

.rhombusRibbonIcon {
    width: 10px;
    height: 10px;
    margin: 0 auto;
}

.rhombusRibbon::before,
.rhombusRibbon::after {
    content: '';
    position: absolute;
    top: 4px;
    left: 0;
    width: 50%;
    height: 1px;
    background-color: var(--c-white);
}

.rhombusRibbon::before {
    left: -9px
}

.rhombusRibbon::after {
    margin-left: 50%;
    left: 9px;
}

.rhombusRibbon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.item {
    text-align: center;
    padding: 23px 0 0;
    cursor: pointer;
}

.itemName {
    text-transform: uppercase;
}

.itemSurname {
    text-transform: uppercase;
    font-weight: 900;
    font-size: 24px;
    margin-bottom: 13px;
}

.value {
    position: relative;
    font-weight: 400;
    font-size: 10px;
    overflow: hidden;
}

.value::before,
.value::after {
    content: '';
    width: 50%;
    position: absolute;
    top: 10px;
    left: -10px;
    height: 1px;
    background-color: var(--c-white);
}

.value::after {
    right: -10px;
    left: auto;
}

.valueIndex {
    display: inline-block;
    width: 20px;
    height: 20px;
    line-height: 18px;
    border: 1px solid var(--c-white);
    border-radius: 10px;
}

@media screen and (min-width: 768px) {
    .root {
        font-size: 24px;
        padding: 275px 100px 40px;
    }

    .rhombusRibbon::before,
    .rhombusRibbon::after {
        height: 2px;
        top: 9px;
    }

    .rhombusRibbonIcon {
        width: 22px;
        height: 22px;
    }

    .rhombusRibbon::before {
        left: -19px
    }

    .rhombusRibbon::after {
        left: 19px
    }

    .itemName {
        line-height: 1;
    }

    .itemSurname {
        font-size: 50px;
    }

    .value::before,
    .value::after {
        top: 20px;
        left: -20px;
    }

    .value {
        font-size: 20px;
        line-height: 1;
    }

    .value::after {
        right: -20px;
        left: auto;
    }

    .valueIndex {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        line-height: 34px;
    }
}
