.root {
    padding: 130px 40px 0;
    font-family: var(--font-lato);
}

.menu {
    padding-top: 33px;
}

.menuItemTitle {
    font-size: 24px;
    font-weight: 900;
    line-height: 1;
    text-transform: uppercase;
    color: var(--c-white);
    margin-bottom: 40px;
    text-align: center;
    cursor: pointer;
}

.rhombusRibbon {
    position: relative;
    overflow: hidden;
}

.rhombusRibbonIcon {
    width: 10px;
    height: 10px;
    margin: 0 auto;
}

.rhombusRibbon::before,
.rhombusRibbon::after {
    content: '';
    position: absolute;
    top: 4px;
    left: 0;
    width: 50%;
    height: 1px;
    background-color: var(--c-white);
}

.rhombusRibbon::before {
    left: -9px
}

.rhombusRibbon::after {
    margin-left: 50%;
    left: 9px;
}

.rhombusRibbon svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.gotoRG {
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    padding-top: 14px;
    padding-bottom: 18px;
    border-bottom: 1px solid var(--c-white);
}

.gotoRGLink {
    display: block;
    position: relative;
    min-width: 40px;
    min-height: 12px;
    max-width: 40px;
    max-height: 12px;
    margin-left: 10px;
}

.gotoRGLink svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.gotoRGtitle {
    font-size: 16px;
    font-weight: 600;
    color: var(--c-white);
    text-transform: uppercase;
    line-height: 1;
    min-height: 12px;
}

.mobileSocial {
    display: flex;
    justify-content: center;
    padding-top: 40px;
}

@media screen and (min-width: 768px) {
    .root {
        padding: 275px 100px 0;
    }

    .rhombusRibbon::before,
    .rhombusRibbon::after {
        height: 2px;
        top: 9px;
    }

    .rhombusRibbonIcon {
        width: 22px;
        height: 22px;
    }

    .rhombusRibbon::before {
        left: -19px
    }

    .rhombusRibbon::after {
        left: 19px
    }

    .menuItemTitle {
        font-size: 38px;
        line-height: 1;
        margin-bottom: 48px;
        position: relative;
    }

    .gotoRG {
        padding-top: 28px;
        padding-bottom: 32px;
        border-bottom: 2px solid var(--c-white);
    }

    .gotoRGtitle {
        font-size: 32px;
        line-height: 1;
    }

    .gotoRGLink {
        min-height: 25px;
        min-width: 84px;
        max-height: 25px;
        max-width: 84px;
    }
}