.videoWrapper {
    position: relative;
    margin: 0 auto;
    width: 240px;
    height: 136px;
    background-color: var(--c-black);
}

.videoWrapperMiddle {
    width: 240px;
    height: 136px;
}

.video {
    border: 0 none;
    outline: none;
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.title {
    font-size: 30px;
    font-family: var(--font-lato);
    font-weight: 600;
    text-transform: uppercase;
    color: var(--c-white);
    text-align: center;
    margin-bottom: 16px;
}

.gotoLinkWrapper {
    text-align: center;
    padding-top: 70px;
}

.gotoLink {
    position: relative;
    width: 203px;
    height: 30px;
    display: inline-block;
}

.gotoLink svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@media screen and (min-width: 768px) {
    .videoWrapper {
        width: 570px;
        height: 332px;
    }

    .videoWrapperMiddle {
        width: 570px;
        height: 332px;
    }

    .gotoLink {
        width: 406px;
        height: 60px;
    }
}

@media screen and (min-width: 1024px) {
    .videoWrapper {
        width: 637px;
        height: 360px;
    }

    .videoWrapperMiddle {
        width: 637px;
        height: 360px;
    }

    .gotoLink {
        width: 270px;
        height: 40px;
    }

    .gotoLinkWrapper {
        padding-top: 30px;
    }
}

